<template>
  <a-layout id="app">
    <a-layout-header class="header">
      <div class="logo" />
      <a-menu
        theme="dark"
        breakpoint="lg"
        mode="horizontal"
        :defaultSelectedKeys="['0']"
        :style="{ lineHeight: '64px', background: 'none' }"
        style="box-shadow: rgba(-2, 0, 0, 0.14) 0 16px 24px 10px, rgba(-2 0, 0, 0.12) 0 6px 30px 5px, rgba(0, 0, 0, 1) 0 8px 20px -5px }"
      >
        <a-menu-item key="0"><router-link to="/">Home</router-link></a-menu-item>
        <a-menu-item key="1"><router-link to="/my">My UV</router-link></a-menu-item>
      </a-menu>
      <div style="float: right; transform: translateY(-4.5em);" v-if="this.$store.state.authenticate.token.length === 0">
        <router-link to="/login"><a-button type="dashed">Login</a-button></router-link>
      </div>
      <div style="float: right; transform: translateY(-4.5em); color: rgba(255, 255, 255, 0.65)" v-else>
        {{ this.$store.state.authenticate.username }} &nbsp; <a-button type="danger" @click="logout">Logout</a-button>
      </div>
    </a-layout-header>
    <transition name="slide-fade" mode="out-in">
      <router-view name="App"></router-view>
    </transition>
    <a-layout-footer style="text-align: center; background: rgba(240,242,245,.5);">
      Copyright © 2012-{{ thisYear }}. The International Conference on Universal Village. All Rights Reserved.
    </a-layout-footer>
  </a-layout>
</template>

<script>
export default {
  name: 'App',
  created () {
    this.updateBreadcrumb()
  },
  watch: {
    '$route': 'updateBreadcrumb'
  },
  methods: {
    updateBreadcrumb() {
      console.log(this.$store.state.navPageList)
      this.$store.state.navPageList = this.$route.path.substr(1).split('/').filter(Boolean)
      this.$store.state.navPageList.unshift('Home')
      console.log(this.$store.state.navPageList)
    },
    logout () {
      this.$store.state.authenticate.username = '';
      this.$store.state.authenticate.token = '';
      this.$http.get(this.$store.state.endpoint + '/logout').then(response => {
        if (response.body.flag === true) {
          this.$message.success('Logout: Successfully.', 3)
          this.$store.state.authenticate.username = '';
          this.$store.state.authenticate.token = '';
          setTimeout(() => {
            this.$router.push('/')
          }, 3000)
        } else {
          this.$message.error('Logout: Failed. Please try again later.', 10);
        }
      }, response => {
        this.$message.error('Logout: Internal Server Error. Please contact administrator.', 10);
      })

    }
  },
  computed: {
    thisYear () {
      return new Date().getFullYear()
    }
  }
}
</script>

<style>
  .disable-activation .ant-menu-item-selected {
    background: none !important;
  }
  body {
    background: #96c93d;
    background: -webkit-linear-gradient(195deg, #96c93d, #00b09b);
    background: linear-gradient(195deg, #96c93d, #00b09b);
    color: #212121;
    background-repeat: no-repeat;
    background-attachment: fixed;
  }
  .header {
    background: rgba(0, 0, 0, .8);
  }
  #app {
    color: #2c3e50;
    transition: all 1s cubic-bezier(0.77, 0.00, 0.175, 1.00);
    background: none;
    min-height: calc(100vh - 128px);
  }
  .slide-fade-enter-active {
    transition: all 175ms cubic-bezier(0.0, 0.0, 0.2, 1);
  }
  .slide-fade-leave-active {
    transition: all 200ms cubic-bezier(0.4, 0.0, 1, 1);
  }
  .slide-fade-enter {
    filter: blur(5px);
    opacity: 0;
  }
  .slide-fade-leave-to {
    filter: blur(5px);
    opacity: 0;
  }
</style>



