<template>
  <a-layout style="background: none !important;">
    <a-layout-sider width="200" style="background: none">
      <a-menu
        mode="inline"
        :style="{ height: '100%', borderRight: 0, background: 'rgba(255, 255, 255, .5)' }"
      >
        <a-sub-menu key="sub1">
          <span slot="title"><a-icon type="solution" />My Submissions</span>
          <a-menu-item key="1"><router-link to="/my/submissions/paper"><a-icon type="copy" />Papers</router-link></a-menu-item>
        </a-sub-menu>
        <a-sub-menu key="sub2" v-if="submenu2enabled">
          <span slot="title"><a-icon type="profile" />My Profile</span>
          <a-menu-item key="5"><router-link to="/my/profile/general"><a-icon type="setting" />General</router-link></a-menu-item>
          <a-menu-item key="6"><router-link to="/my/profile/academic"><a-icon type="book" />Academic</router-link></a-menu-item>
        </a-sub-menu>
      </a-menu>
    </a-layout-sider>
    <a-layout style="padding: 0 24px 24px; background: none !important;">
      <a-breadcrumb style="margin: 16px 0">
        <a-breadcrumb-item v-for="navPage in this.$store.state.navPageList" :key="navPage">
          {{ navPage | capitalize }}
        </a-breadcrumb-item>
      </a-breadcrumb>
      <a-layout-content :style="{ background: 'rgba(255, 255, 255, .5)', padding: '24px', margin: 0, minHeight: '280px' }">
        <router-view></router-view>
      </a-layout-content>
    </a-layout>
  </a-layout>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      submenu2enabled: false
    }
  },
  created () {
    this.checkLoginStatus()
  },
  methods: {
    checkLoginStatus () {
      if (this.$store.state.authenticate.token.length === 0) {
        this.$message.info('Before entering My UV, please log in.', 10)
        this.$router.push('/login')
      } else if (this.$store.state.authenticate.username.length === 0) {
        this.$message.info('Before entering My UV, please log in.', 10)
        this.$router.push('/login')
      }
    }
  }

}
</script>

<style scoped>

</style>



